import { SettingsProvider } from './../../providers/settings/settings';
import { LoginPage } from './../login/login';
import { AuthProvider } from './../../providers/auth/auth';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { PatternLockPage } from '../pattern-lock/pattern-lock';
import { Settings } from '../../models/settings.model';
// import { SettingsProvider } from '../../providers/settings/settings';

/**
 * Generated class for the SettingsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {

   // Our local settings object
   options: any;
   settingsReady = false;
   t = false;
 

  constructor(public navCtrl: NavController, public settings: SettingsProvider,public navParams: NavParams, public auth: AuthProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SettingsPage');
  }

  ionViewWillEnter() {
    console.log('ionViewWillEnter');

    this.settings.load().then(() => {
      this.settingsReady = true;
      this.options = this.settings.allSettings;
      console.log('settings settigns', this.options);
    });
  }


  editPattern(){
    console.log('editPattern', this.options.patternProtect.protect);
    var type = '';
    if(this.options.patternProtect.protect){
      type = 'edit';
    }else{
      type = 'remove';
    }
    this.navCtrl.push(PatternLockPage, { type: type });
  }

  signOut(){
    this.auth.logout().then(res => {
      this.settings.setAll(new Settings());
      this.navCtrl.setRoot(LoginPage);
    }, err => {});
  }

}



