
import 'rxjs/add/operator/toPromise';

import { Injectable } from '@angular/core';
import { ApiProvider } from '../api/api';
import { environment } from '../../environments/environments'

/*
  Generated class for the AuthProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AuthProvider {

  _user: any;

  constructor(public api: ApiProvider) {
    api.getDbLocal(environment.userAuthName).then(res => {
      this._user = res;
      console.log('auth user',this._user);
    });;
  }

  /**
   * Send a POST request to our login endpoint with the data
   * the user entered on the form.
   */
  login(accountInfo: any) {
    let seq = this.api.post('usuarios/loginApp', accountInfo);
    seq.subscribe((res: any) => {
        console.log("resLogin", res);
        // If the API returned a successful response, mark the user as logged in
        if (res.status) {
          this._loggedIn(res);
        } else {

        }
      }, err => {
        console.error('ERROR', err);
      });

    return seq;
  }



  /**
   * Log the user out, which forgets the session
   */
  logout(): Promise<any> {
    return new Promise((resolve, reject) => {
      this._user = null;
      this.api.removeDbLocal(environment.userAuthName).then(res => {resolve(true)}, err => {reject(false)});
    });
  }

  /**
   * Process a login/signup response to store user data
   */
  _loggedIn(resp){
    this._user = resp;
    console.log('_loggedIn',this._user);
    this.api.saveDbLocal(environment.userAuthName, this._user);
  }




}



